 .title {
    text-align: center;
    color: rgb(250, 203, 145);
    text-shadow:  2px 2px black;
    font-weight: 800;
 }
 
 .card{
   width: 200px;
   height: 200px;
   border: 2px solid black;
   border-radius: 10px;
   background-color: whitesmoke;
 }

 .cardContainer {
   display: grid;
   grid-template-columns: repeat(6, 1fr);
 }

 .gameContainer {
  min-height: 100vh
 }
 
 .scoreBoard {
  margin: 20px auto;
  font-size: 30px;
  background-color: white;
  width: 200px;
  border: 3px solid black;
 }
 .gameOver {
   background-color: red;
 }